.form {
  max-width: 800px;
  color: #faebd7;
  background-color: rgba(0, 0, 0, 0);
  flex-direction: column;
  gap: 30px;
  margin: auto;
  padding: 12px;
  font-family: Roboto, sans-serif;
  font-size: 48px;
  font-weight: 500;
  line-height: 1.5;
  display: flex;
}

label {
  flex-direction: column;
  display: flex;
}

label:last-of-type {
  margin-bottom: 60px;
}

button[type="submit"] {
  cursor: pointer;
  min-width: 500px;
  min-height: 60px;
  color: #11121c;
  transition-property: background-color, color, box-shadow;
  transition-duration: var(--time-duration);
  transition-timing-function: var(--timing-function);
  background: silver;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: auto;
  padding: 8px 12px;
  font-size: 48px;
  display: block;
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button[type="submit"]:-webkit-any(:focus, :hover) {
  color: #fff;
  background-color: rgba(53, 56, 56, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button[type="submit"]:is(:focus, :hover) {
  color: #fff;
  background-color: rgba(53, 56, 56, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button[type="submit"]:active {
  color: #e4e059;
  background-color: rgba(26, 26, 26, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

input {
  font: inherit;
  padding: 8px;
  box-shadow: 0 2.8px 2.2px rgba(241, 239, 239, .035), 0 6.7px 5.3px rgba(229, 228, 228, .047), 0 12.5px 10px rgba(232, 229, 229, .06), 0 22.3px 17.9px rgba(234, 232, 232, .07), 0 41.8px 33.4px rgba(240, 239, 239, .086), 0 100px 80px rgba(249, 246, 246, .12);
}

.link-wrap-last {
  max-width: 1000px;
  justify-content: flex-start;
  gap: 240px;
  margin: auto;
  padding-top: 30px;
  padding-left: 40px;
  display: flex;
}

